.image {
  height: 400px;
  width: 300px;
  margin: 5px 5px 0px 0px;
}

.rect {
  opacity: 0.5;
  border: 4px solid black;
  position: absolute;
  background-color: none;
}

.flip {
  display: flex;
  margin: 5px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.changeImg {
  display: flex;
  margin: 5px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.results {
  text-align: left;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-spacing: 10px;
  border-collapse: separate;
}